* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Work Sans', sans-serif;
}

:root {
  --theme-blue: #00AEEF;
}

p {
  font-size: 16px;
}

a {
  text-decoration: none;
  color: black;
  font-size: 16px;
}
.main-container {
  min-height: 90vh;
  display: flex;
  flex-direction: column;
  background-color:var(--background-color);
}
.emptyList-wrap {
  display: flex;
  justify-content: center;
}
.emptyList-wrap img {
  max-width: 500px;
  width: 100%;
}

.grid-2{
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 3rem;
}
.grid-3{
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 2rem;
}
.grid-4{
  /* display: flex; */
  /* margin-top: 20px; */
  /* width: 100%; */
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 2rem;
  /* margin: 2rem; */

}
.grid-1-3 {
  display: grid;
  grid-template-columns: 25% auto;
  grid-gap: 3rem;
}
.grid-3-1 {
  display: grid;
  grid-template-columns: auto 25% ;
  grid-gap: 3rem;
}
.grid-1-2 {
  display: grid;
  grid-template-columns: 33% auto;
  grid-gap: 3rem;
}
.section {
  margin: 10px 10vw;
}




/* .................... */
/* Banner */
/* .................... */
#head {
  height: 60vh;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  background: rgba(0, 0, 0, 0.6) url(./assets/images/banner.jpg);
  background-size: cover;
  background-attachment: fixed;
  background-position: center;
  background-blend-mode: overlay;
  color: white;
  padding: 0 10vw;
  /* margin-top: 100px; */
  z-index: 0;
}

.banner-content {
  align-items: center;
  text-align: center;
  justify-content: center;
  margin: 20px;

}

.banner-content h1 {
  color: var(--theme-blue);
  font-size: 74px;
  text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.1);
  font-weight: 700;
  text-align: center;
  font-family: "Open sans", Helvetica, Arial;
}

.banner-content h2 {
  color: #fff;
  font-size: 24px;
  text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.1);
  font-weight: 700;
  text-align: center;
  font-family: "Open sans", Helvetica, Arial;

}


.home-video {
  width: 100%;
  background: #151414;

}
.home-video video{
  width: 100vw;
  
}

.about-section{
  margin: 20px 0px;
}
.about-section p{
  font-size: 24px;

}


.team-cards{
  text-align: center;

}
.team-cards h2{

}
.team-cards h3{
  
}
.team-cards img{
  width: 100%;
}

.sports-card{
  text-align: center;

}
.sports-card img{
  width: 100%;
}
.events-card{
  text-align: center;

}
.events-card img{
  width: 100%;
}
.recent-events-item img{
  width: 100%;
}







/* Contact */
.contact-us-form {
  margin-bottom: 20px;

}

.form-control {
  width: 100%;
  padding: 10px;
  margin: 5px 0px;
  font-size: 14px;

}

.submit-button {
  padding: 10px 15px;
  color: #000000;
  background-color: white;
  font-size: 16px;
  font-family: 'Maven Pro', sans-serif;
  margin-bottom: 10px;
  border: 2px solid rgb( 0,59,117 );
  border-radius: .25rem;
  transition: .3s;
}

.submit-button:hover {
  background-color: rgb( 0,59,117 );
  color: white;
  cursor: pointer;
}

.success-message {
  font-size: 16px;
  color: rgb( 0,59,117 );
  text-align: center;
  margin-bottom: 20px;
}











.sports-page-img img{
  width: 100%;
}














/* ......................... */
/* Footer */
/* ......................... */

.footer-section {
  background: #151414;
  position: relative;
}
.footer-container{
  margin: 0px 10vw;
}
.footer-cta {
  border-bottom: 1px solid #373636;
  padding: 20px 0px;
}

.footer-content {
  position: relative;
  z-index: 2;
  padding: 20px 0px;
}
.footer-pattern img {
  position: absolute;
  top: 0;
  left: 0;
  height: 100px;
  background-size: cover;
  background-position: 100% 100%;
}
.footer-logo {
  margin-bottom: 30px;
}
.footer-logo img {
    max-width: 100px;
}
.footer-text p {
  margin-bottom: 14px;
  font-size: 1rem;
  color: #7e7e7e;
  line-height: 28px;
}


.footer-widget-heading h3 {
  color: #fff;
  font-size: 20px;
  font-weight: 600;
  margin-bottom: 40px;
  position: relative;
  font-family: 'Maven Pro', sans-serif;
}
.footer-widget-heading h3::before {
  content: "";
  position: absolute;
  left: 0;
  bottom: -15px;
  height: 2px;
  width: 50px;
  background:  var(--theme-blue);
}
.footer-widget ul li {
  display: inline-block;
  width: 50%;
  margin-bottom: 12px;
}
.footer-widget ul li a:hover{
  color:  var(--theme-blue);
}
.footer-widget ul li a {
  color: #878787;
  text-transform: capitalize;
}

.copyright-area{
  background: #202020;
  padding: 30px 10vw;

  
}
.copyright-text p {
  margin: 0;
  font-size: 14px;
  color: #878787;
}
.copyright-text p a{
  color:  var(--theme-blue);
}
.footer-menu {
    text-align: right;
  
  }
.footer-menu li {
  display: inline-block;
  margin-left: 20px;
  text-align: right;

}
.footer-menu li:hover a{
  color:  var(--theme-blue);
}
.footer-menu li a {
  font-size: 14px;
  color: #878787;
}